import { Heading, Heading1Data, Paragraph } from '@ali/4ever-mo';
import { Block, BlockChildren } from '@ali/4ever-cangjie';
import { isEmpty } from 'lodash-es';

const { Heading1 } = Heading;

export { Heading1 };

export type Subject = Block<Heading1Data, BlockChildren>;

export function isSubject(node: any): node is Subject {
  return Block.isBlock(node) && node.type === 'subject-group';
}

export interface SubjectProps {
  key?: string;
  data: Heading1Data;
  nodes?: Block[];
}

export function createSubject(props: SubjectProps): Subject {
  return Block.create({
    ...props,
    type: 'subject-group',
  });
}

export function isNormalParagraph(node: Block): boolean {
  return Paragraph.isParagraph(node) && isEmpty(node.data);
}
